<template><section><h2>Dialog 弹出框</h2>
<h3>基础用法</h3>
<div class="demo-block mt_15">
  <tj-button round="3px" @click="handle">alert</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle2">confirm</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle3">prompt</tj-button>
  
  
</div>
<demo-block>
                    <div slot="desc">  <tj-button round="3px" @click="handle">alert</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle2">confirm</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle3">prompt</tj-button>

  
</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle"</span>&gt;</span>alert<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle2"</span>&gt;</span>confirm<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle3"</span>&gt;</span>prompt<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        handle() {
          <span class="hljs-keyword">this</span>.$dialog.alert(<span class="hljs-string">'操作成功!'</span>, <span class="hljs-string">'提示'</span>);
        },
        handle2() {
          <span class="hljs-keyword">this</span>.$dialog.confirm(<span class="hljs-string">'确定执行此操作?'</span>, <span class="hljs-string">'提示'</span>).then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'已确认'</span>)
          }).catch(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'已取消'</span>)
          });
        },
        handle3() {
          <span class="hljs-keyword">this</span>.$dialog.prompt(<span class="hljs-string">' '</span>, <span class="hljs-string">'请输入姓名'</span>).then(<span class="hljs-function">(<span class="hljs-params">{ value }</span>) =&gt;</span> {
            <span class="hljs-keyword">if</span> (value) {
              <span class="hljs-keyword">this</span>.$dialog.alert(<span class="hljs-string">`你的名字是 <span class="hljs-subst">${ value }</span>`</span>, <span class="hljs-string">'输入成功'</span>);
            }
          });
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>参数传递</h3>
<div class="demo-block mt_15">
  <tj-button round="3px" @click="handle4">参数传递</tj-button>
</div>
<demo-block>
                    <div slot="desc"><div class="demo-block mt_15">
  <tj-button round="3px" @click="handle4">参数传递</tj-button>
</div>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block mt_15"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle4"</span>&gt;</span>参数传递<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      handle4() {
        <span class="hljs-keyword">this</span>.$dialog.confirm(<span class="hljs-string">''</span>, {
          <span class="hljs-attr">title</span>: <span class="hljs-string">''</span>,
          <span class="hljs-attr">message</span>: <span class="hljs-string">'&lt;div ref="html"&gt;&lt;h3&gt;title&lt;/h3&gt;&lt;p&gt;this a content&lt;/p&gt;&lt;/div&gt;'</span>,
          <span class="hljs-attr">modalFade</span>: <span class="hljs-literal">false</span>,
          <span class="hljs-attr">confirmButtonText</span>: <span class="hljs-string">'confirm'</span>,
