</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>message</td>
<td>文本内容</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>showConfirmButton</td>
<td>是否显示确认按钮</td>
<td>boolean</td>
<td></td>
<td>true</td>
</tr>
<tr>
<td>showCancelButton</td>
<td>是否显示取消按钮</td>
<td>boolean</td>
<td></td>
<td>true</td>
</tr>
<tr>
<td>confirmButtonText</td>
<td>确认按钮文字</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>cancelButtonText</td>
<td>取消按钮文字</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>showInput</td>
<td>是否显示一个输入框</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>inputType</td>
<td>输入框的类型</td>
<td>string</td>
<td></td>
<td>text</td>
</tr>
<tr>
<td>inputValue</td>
<td>输入框的值</td>
<td>string</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .tj-dialog {width: 370px}
  </style>
<script>
    export default {
      methods: {
        handle() {
          this.$dialog.alert('操作成功!', '提示');
        },
        handle2() {
          this.$dialog.confirm('确定执行此操作?', '提示').then(() => {
            console.log('已确认')
          }).catch(() => {
            console.log('已取消')
          });
        },
        handle3() {
          this.$dialog.prompt(' ', '请输入姓名').then(({ value }) => {
            if (value) {
              this.$dialog.alert(`你的名字是 ${ value }`, '输入成功');
            }
          });
        },
        handle4() {
          this.$dialog.confirm('', {
            title: '',
            message: '<div ref="html"><h3>title</h3><p>this a content</p></div>',
            modalFade: false,
            confirmButtonText: 'confirm',
            cancelButtonText: 'cancel',
          }).then(({ value }) => {
            if (value) {
              this.$dialog.alert(`你的名字是 ${ value }`, '输入成功');
            }
          });
        }
      }
    }
  </script>


